import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { onGetUsersData } from '../../../redux/actions';
import Box from '@material-ui/core/Box';
// import AppAnimate from '../../../@crema/core/AppAnimate';
import GridContainer from '../../../@crema/core/GridContainer';
import Grid from '@material-ui/core/Grid';
import IntlMessages from '../../../@crema/utility/IntlMessages';
import { Fonts } from '../../../shared/constants/AppEnums';
// import { AppState } from '../../redux/store';
// import PinedUsers from './PinedUsers';
import QuickStats from './QuickStats'
import SalesState from './SalesState'
import OrderNTransaction from './OrderNTransaction'

const Dashboard = () => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(onGetUsersData());
    }, [dispatch]);

    // const { total, active, blocked, unattended } = useSelector<AppState, AppState['users']>(
    //     ({ users }) => users,
    // );
    const data = {
        clientsData: {
            count: '3100'
        },
        invoiceData: {
            count: '450'
        },
        totalProjectsData: {
            count: '2420'
        },
        openProjectsData: {
            count: '790'
        }
    }
    const salesState = [
        {
            bgColor: "#49BD65",
            icon: "/assets/images/dashboard/1_monthly_sales.png",
            id: 2,
            type: "Last Month Sale",
            value: "6,257",
        },
        {
            bgColor: "#3A3849",
            icon: "/assets/images/dashboard/1_email_sent.png",
            id: 4,
            type: "Total Email Sent",
            value: "11,320",
        },
    ];
    const transactionData = [
        {
            id: '#SK231',
            customer: 'Ina Hughes',
            date: '08-21-2020',
            paymentType: 'COD',
            status: 'In Transit',

        },
        {
            id: '#SK232',
            customer: 'Myrtie Ferguson',
            date: '08-12-2020',
            paymentType: 'Prepaid',
            status: 'Delivered',

        },
        {
            id: '#SK233',
            customer: 'Johnny Herrera',
            date: '07-30-2020',
            paymentType: 'Prepaid',
            status: 'In Transit',

        },
        {
            id: '#SK234',
            customer: 'Barbara Bowers',
            date: '07-25-2020',
            paymentType: 'COD',
            status: 'Delivered',

        },
        {
            id: '#SK235',
            customer: 'Annie Herrera',
            date: '07-11-2020',
            paymentType: 'Refunded',
            status: 'In Transit',

        },
        {
            id: '#SK236',
            customer: 'Ann Herrington',
            date: '06-21-2020',
            paymentType: 'COD',
            status: 'Return',

        },
        {
            id: '#SK237',
            customer: 'Ina Hughes',
            date: '06-12-2020',
            paymentType: 'COD',
            status: 'In Transit',

        }
    ];

    return (
        <>
            <Box pt={{ xl: 4 }}>
                <Box
                    component='h2'
                    color='text.primary'
                    mb={{ xs: 4, sm: 4, xl: 6 }}
                    fontSize={16}
                    fontWeight={Fonts.BOLD}>
                    <IntlMessages id='dashboard.metrics' />
                </Box>

                <GridContainer>
                    <Grid item xs={12} md={8}>

                        <Grid item xs={12} md={12}>
                            <QuickStats quickStatsData={data} />
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={4}>

                        {salesState.map((state, index) => (
                            <Grid item xs={12} sm={12} md={12} key={index} style={{ paddingBottom: '32px' }}>
                                <SalesState state={state} />
                            </Grid>
                        ))}
                    </Grid>
                </GridContainer>
            </Box>
            <Box marginTop={10}>
                <OrderNTransaction transactionData={transactionData} />
            </Box>
        </>
    );
};

export default Dashboard;
